import React, { useState, useEffect } from "react";
import { motion, useScroll } from "framer-motion";
import { Link } from "react-router-dom";
import BackgroundChange from "./backgroundChage";


function Header({ refs }) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isBlackout, setBlackout] = useState(false);
  const [isMenu, setIsMenu] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const { scrollYProgress } = useScroll();

  useEffect(() => {
    const handleResize = () => {
      setIsMenu(window.innerWidth <= 480);
    };

    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };
    window.addEventListener('resize', handleResize);
    window.addEventListener('scroll', handleScroll);

    handleResize(); // Call initially
    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    if (isMenuOpen) {
      document.body.style.overflow = "hidden"; 
    } else {
      document.body.style.overflow = ""; 
    }
  }, [isMenuOpen]);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
    setBlackout(!isBlackout);
  };

  const scrollToRef = (ref, isPage1 = false) => {
    if (ref && ref.current) {
      const offset = isPage1 ? -150 : -65; 
      const topPos = ref.current.getBoundingClientRect().top + window.scrollY;
      window.scrollTo({ top: topPos + offset, behavior: "smooth" });
    }
  };
  return (
    <header>
      {!isMenu && (
      <div className="header-container">
        <div className={scrolled ? 'header scrolled' : 'header'}>
          <div style={{cursor: "pointer"}} onClick={() => {window.location.href = "/";}}>
            <h1>Samuel Akintomide</h1>
            <h4>Robotics Engineer, Developer, and Designer</h4>
          </div>
          <div className="nav-buttons-container">
          <Link to="/"><button className="nav-buttons" onClick={() => {scrollToRef(refs.page1, true);}}>Home</button></Link>
            <Link to="/"><button className="nav-buttons" onClick={() => {scrollToRef(refs.page2)}}>Skills</button></Link>
            <Link to="/"><button className="nav-buttons" onClick={() => {scrollToRef(refs.page3)}}>Projects</button></Link>
            <Link to="/"><button className="nav-buttons" onClick={() => {scrollToRef(refs.page4)}}>FAQ</button></Link>
          </div>
        </div>
        <motion.div className="progress-bar" style={{ scaleX: scrollYProgress }} />
        <BackgroundChange />
      </div>
      )}
      {isMenu && (
        <>
          <button className="menuButton" onClick={toggleMenu}>MENU</button>
          <div className={isMenuOpen ? 'menu open' : 'menu'}>
            <button className="innerButton" onClick={toggleMenu}>CLOSE</button>
            <BackgroundChange />
            <div className="nav-buttons-container">
            <Link to="/"><button className="nav-buttons" onClick={() => { toggleMenu(); scrollToRef(refs.page1, true);}}>Home</button></Link>
            <Link to="/"><button className="nav-buttons" onClick={() => { toggleMenu(); scrollToRef(refs.page2)}}>Skills</button></Link>
            <Link to="/"><button className="nav-buttons" onClick={() => { toggleMenu(); scrollToRef(refs.page3)}}>Projects</button></Link>
            <Link to="/"><button className="nav-buttons" onClick={() => { toggleMenu(); scrollToRef(refs.page4)}}>FAQ</button></Link>
          </div>
          
          </div>
        </>
      )}
      <button className={isBlackout ? 'blurBackground open' : 'blurBackground'} onClick={toggleMenu}></button>
      
    </header>
  );
}

export default Header;
