import React from "react";
import '../../styles/project.scss';
import InfectionSim from "../../components/InfectionSim";

function App() {
  return (
    <body style={{ margin: "0px", overflow:"hidden"}}>
      <div style={{ borderBottom: "2px solid var(--font-color)", height: "70vh", display: "flex", justifyContent: "center", alignContent: "center", paddingBottom:"2px"}}>
        <InfectionSim />
      </div>
      <div className="information">
        <div style={{backdropFilter:"blur(4px)", zIndex:"1", position:"sticky", bottom:"0", display:"flex", flexDirection:"column"}}>
          <h2>Plague In The City</h2>
          <h3 style={{margin:"0 0 30px"}}>AI Disease Spreading Simulation</h3>
          <p style={{color:"var(--red)"}}>The full report documenting the creation of the ARTEx can be provided on request. A breif is provided below.</p>
          <h4 style={{display:"flex", margin:"30px 0 10px"}}>1.0 Introduction</h4> 
          This project uses cutting-edge artificial intelligence to simulate how a pandemic spread can be contained in a city. The goal is to create a digital city where virtual people (agents) act like real residents, moving around, socialising, and potentially spreading a simulated virus. These agents' health is affected by contact with contaminants or infected peers, and without treatment, they can infect others. To stop the spread, special virtual agents equipped with simulated antiviral medications move through the city's hot zones, helping sick individuals recover faster. Virtual treatment centres are strategically placed for infected agents to seek medical help. Advanced AI algorithms control these aspects, creating a dynamic representation of a pandemic from its start to containment < br/><br />
            Researchers can use the simulation to experiment with various factors like health, virus traits, and city planning strategies. This experimentation provides valuable data on real-world infectious threats, improves our understanding of how to respond, and guides public health decisions. In summary, this project relates to AI by utilising advanced algorithms to simulate a city-scale pandemic environment. Virtual agents, guided by AI, emulate realistic behaviours and health dynamics, facilitating experiments with interventions and city planning strategies. The incorporation of specialist agents and treatment centres, driven by AI, enables strategic containment efforts. Overall, AI plays a central role in creating a dynamic digital landscape, providing researchers with unprecedented opportunities to analyse infectious disease dynamics and inform public health decisions.
            <h4 style={{margin:"15px 0 10px"}}>2.1 Viruses and Pandemics in Modern Society</h4>
            Viruses and pandemics are major concerns in our modern world, affecting global health, economies, and daily life. These microscopic infections, like the COVID-19 virus, can rapidly spread globally due to increased travel and globalisation. Pandemics impact not only health but also social, economic, and healthcare aspects of society. To address these challenges, it's essential to invest in research, and strengthen healthcare systems for effective responses to tackling such viral threats.
            <h4 style={{margin:"15px 0 10px"}}>2.2 Applying Plague in the City in Everyday Situations</h4> 
            Visualising the mechanics behind disease spread can inform policy decisions. This project has great potential for public health communication and guidance. There are various ways to apply this project in everyday situations so that people can change their behaviour in better containing outbreaks in populated areas, such as:
            <li style={{listStyle:"square", textDecoration:"none", textTransform:"none", letterSpacing:"none", whiteSpace:"wrap", letterSpacing:"0"}}> Public Health Campaigns:< br/>The simulation results could inform public health campaigns on how diseases spread and the importance of preventative measures like handwashing, masks, and social distancing. The visualisation would resonate with people.</li>
            <li style={{listStyle:"square", textDecoration:"none", textTransform:"none", letterSpacing:"none", whiteSpace:"wrap", letterSpacing:"0"}}>Travel Guidance:< br/>Governments and health organisations could use the simulator to provide guidance on the risk levels of traveling to certain cities during an outbreak for example on the TFL. This could help people make more informed decisions.</li>
            <li style={{listStyle:"square", textDecoration:"none", textTransform:"none", letterSpacing:"none", whiteSpace:"wrap", letterSpacing:"0"}}>School/Workplace Policy:< br/>The simulator could demonstrate the efficacy of closing schools/workplaces early in an outbreak to decision makers. It could inform policies on closures and re-openings.</li>
            <li style={{listStyle:"square", textDecoration:"none", textTransform:"none", letterSpacing:"none", whiteSpace:"wrap", letterSpacing:"0"}}>Individual Behaviour:< br/>By showing how individual behaviours like hygiene, social distancing, and selfisolation impact overall spread, it could encourage people to take precautions seriously if they personally feel at low risk.</li>
            <h4 style={{margin:"15px 0 10px"}}>2.3 Enhancing Navigation Through Environmental Data Collection</h4>
            Integrating environmental data collection into route mapping has great potential for enhancing navigation efficiency. By using sensor technologies and datasets on terrain, obstacles, and landmarks, advanced algorithms can find optimal paths considering landscape variables. In the "Plague in the City" simulation, navigation meshes, and agent parameters improved dynamic movements and obstacle avoidance. Expanding on this, real-time data can help mapping systems offer personalised, environment aware routes. Absorbing comprehensive environmental data allows adaptation to changing landscapes. Combining rich data with smart mapping algorithms sets the stage for the next generation of agile, location-aware navigation services.
        </div>

          <div className="bento-box">
            <div style={{display:"flex", alignContent:"space-around", gridGap:"10px"}}>
              <div className="bento-container1"><img src={require("../../images/template/image1.png")} /></div>
              <div className="side-bento">
                <div className="bento-container2"><img src={require("../../images/template/image2.png")} /></div>
                <div className="bento-container2"><img src={require("../../images/template/image3.png")} /></div>
              </div> 
            </div>
            <div className="bento-container3"><img src={require("../../images/template/image4.png")} /></div>
            <div style={{display:"flex", gridGap:"10px"}}>
              <div className="side-bento">
                <div className="bento-container2"><img src={require("../../images/template/image6.png")} /></div>
                <div className="bento-container2"><img src={require("../../images/template/image7.png")} /></div>
              </div>
              <div className="bento-container1"><img src={require("../../images/template/image5.png")} /></div> 
            </div>
            <div className="bento-container3"><img src={require("../../images/template/image8.png")} /></div>
            <div style={{display:"flex", gridGap:"10px"}}>
              <div className="bento-container1"><img src={require("../../images/template/image9.png")} /></div>
              <div className="side-bento">
                <div className="bento-container2"><img src={require("../../images/template/image10.png")} /></div>
                <div className="bento-container2"><img src={require("../../images/template/image11.png")} /></div>
              </div> 
            </div>
            <div className="bento-container3"><img src={require("../../images/template/image12.png")} /></div>
          </div>
      </div>
    </body>
  );
}

export default App;
