import React, { useEffect, useState, useRef } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom"; // Remove Switch
import { motion, AnimatePresence} from "framer-motion";
// Components
import Background from './components/background';
import Intro from './components/intro';
import Header from "./components/header";
import Footer from "./components/Footer";
// Pages
import Home from './pages/Home';
import ARTEx from './pages/projects/ARTEx';
import Plague from './pages/projects/Plague';
// Styles
import "./App.scss";

function App() {
  const [isShow, setIsShow] = useState(false);
  const [isSmall, setIsSmall] = useState(false);

  const page1Ref = useRef(null);
  const page2Ref = useRef(null);
  const page3Ref = useRef(null);
  const page4Ref = useRef(null);

  const refs = {
    page1: page1Ref,
    page2: page2Ref,
    page3: page3Ref,
    page4: page4Ref
  };

  useEffect(() => {
    const timeout = setTimeout(() => {
      setIsShow(true);
    }, 3500);
    return () => clearTimeout(timeout);
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setIsSmall(window.innerWidth > 320);
    };

    window.addEventListener('resize', handleResize);
    handleResize();
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
                
  return (
    <>
      <Background />
      <Intro />
      <motion.div 
        className="content"
        initial={{opacity: 0, y: 25 }} 
        animate={{ opacity: isShow ? 1 : 0, y: isShow ? 0 : 50 }} 
        transition={{ delay: 0.3, duration: 0.4, ease: "easeInOut" }}>
      <Router>
        <Header refs={refs} />
        <Routes> 
          <Route
            path="/"
            element={<Home refs={refs} />}
          />
          <Route
            path="/artex"
            element={<ARTEx />}
          />
          <Route
            path="/plague"
            element={<Plague />}
          />
          <Route
            path="/Bank"
            element={<ARTEx />}
          />
          <Route
            path="/Exploit"
            element={<ARTEx />}
          />
          <Route
            path="/Line"
            element={<ARTEx />}
          />
          <Route
            path="/Store"
            element={<ARTEx />}
          />
        </Routes>
        <Footer />
      </Router></motion.div>
    </>
  );
}

export default App;
