import React, { useEffect, useState } from "react";
import { motion, AnimatePresence} from "framer-motion";
import "../App.scss";
import intro from "../styles/intro.module.scss";


function Intro() {
  const [isAnimationComplete, setIsAnimationComplete] = useState(false);
  const [isIntro, setIsIntro] = useState(true);
  const [isShow, setIsShow] = useState(true);
  const [isDone, setIsDone] = useState(true);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const isMobile = windowWidth <= 675;

  useEffect(() => {
    const handleWindowResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener("resize", handleWindowResize);
    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);
  return (
    <>
    {isShow && (
      <div className={intro.loading}>
        <AnimatePresence >
          {!isAnimationComplete && (
            <motion.div
              className={intro.load}
              key="image"
              initial={{ scale: 0 }}
              animate={{ rotate: [0, 360], scale: [0, 1] }}
              transition={{ delay: 0.2, type: "spring", stiffness: 260, damping: 20, duration: 0.5 }}
              onAnimationComplete={() => {setIsAnimationComplete(true); setIsIntro(false);}}
              exit={{ opacity: 0, transition: { duration: 0.5, stiffness: 260, damping: 20 } }}>
              <img src={require("../images/shoto.gif")} alt="shoto" />
            </motion.div>
          )}
        </AnimatePresence>

        <AnimatePresence>
          {!isIntro && (
            <motion.div
              className={intro.display}
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1 }}
              onAnimationComplete={() => {setIsIntro(true); setIsDone(true);}}
              exit={{ opacity: 0, transition: { delay:1, duration: 0.5, stiffness: 260, damping: 20 } }}>
              <h1>Samuel Akintomide</h1>
              <h4 className="intro-h4">Robotics Engineer, Developer, and Designer</h4>
            </motion.div>
          )}
        </AnimatePresence>

        <AnimatePresence>
          {!isDone && (
            <motion.div
              className={intro.display}
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1 }}
              onAnimationComplete={() => {setIsDone(true); setIsShow(false);}}
              exit={{ opacity: 0}}/>
          )}
        </AnimatePresence>
        
      </div>
    )}
      </>
  );
}

export default Intro;
