import React from "react";
import footerCSS from "../styles/footer.module.scss";

const Header = () => {
  return (
    <footer className={footerCSS.footer}>
      <ul className={footerCSS.ul} style={{marginRight:"20px"}}>
        <li className={footerCSS.links} style={{letterSpacing:"2px"}} href="https://www.framer.com/"><h5>Contact Me</h5></li>
        <li className={footerCSS.links} href="https://www.framer.com/"><h5>linkedin</h5></li>
        <li className={footerCSS.links} href="https://www.framer.com/"><h5>Email</h5></li>
        <li className={footerCSS.links} href="https://www.framer.com/"><h5>Phone</h5></li>
      </ul>
      <ul className={footerCSS.ul} style={{marginRight:"20px"}}>
        <li className={footerCSS.links} href="https://www.framer.com/"><h5>Help</h5></li>
      </ul>
      <ul className={footerCSS.ulHide} style={{marginRight:"20px"}}>
        <li className={footerCSS.links} href="https://www.framer.com/"><h5>Help</h5></li>
      </ul>
      <div className={footerCSS.ulRight}>
        <ul className={footerCSS.ulR}>
          <li className={footerCSS.title}>Found in</li>
          <li><h5>United Kingdom</h5></li>
          <li><h5>London</h5></li>
        </ul>
      </div>
    </footer>
  );
};

export default Header;
